import React, {FC} from 'react';
import { card } from "./CardStyles";
import Link from 'next/link';

interface Props {
    id?: string;
    title?: string;
    children?: any;
}

export const Card: FC<Props> = ({id, title, children }) => {
    return (
        <card.Item>
            <card.ItemWrapper>
                <Link href={`/posts/${id}`}>
                    <card.Title title={ title} >{ title }</card.Title>
                </Link>
                { children }
            </card.ItemWrapper>
        </card.Item>
    );
}